<template>
    <div class="image-view-container">
        <img class="the-image" :src="imageUrl">
    </div>
</template>

<script>
    import imgurService from '@/service/ImgurService.js';

    export default {
        name: 'ImageView',

        data() {
            return {
                albumImages: null,
                imageUrl: '',
            }
        },

        mounted() {
            this.slideshow();
        },

        methods: {
            slideshow() {
                this.updateAlbum();
            },

            async getAlbumImages() {
                if (this.albumImages === null) {
                    this.albumImages = await imgurService.getAlbumImages(this.$route.params.album);
                }

                const images = this.albumImages.data
                    .map(r => r.id)
                    .map(i => imgurService.getImageUrl(i));

                return images;
            },

            async updateAlbum() {
                const images = await this.getAlbumImages();

                const randomIndex = Math.floor(Math.random() * images.length);

                const image = new Image();

                image.onload = () => {
                    this.imageUrl = image.src;

                    setTimeout(this.slideshow, this.getDelay());
                };

                image.src = images[randomIndex];
            },

            getDelay() {
                const delay = this.$route.params.delay;

                if (delay) {
                    return Number(delay) * 1000;
                }

                return 5000;
            }
        },
    }
</script>

<style>
    .image-view-container {
        width: 100%;
        height: 100%;
        display: flex;
    }

    .the-image {
        max-width: 100%;
        max-height: 100%;
        margin: auto;
    }
</style>
